<template>
    <div class="question page">
        <h2 v-text="selectedQuestion.text"></h2>
        <form @submit.prevent>
          <ul>
            <li v-for="(choice, index) in selectedQuestion.choices" :key="index">
              <input v-model="chosen" type="radio" name="choice" :id="`choice_${index}`" :value="choice.text">
              <label :for="`choice_${index}`" v-text="choice.text"></label>
            </li>
          </ul>
          <button class="route-btn" @click="saveAndNext(selectedQuestion)" v-if="nextActive">Next Question</button>
          <button class="route-btn" @click="saveAndFinish(selectedQuestion)" v-else>Finish Exam</button>
        </form>
    </div>
</template>

<script>
import { mapGetters } from "vuex";
import { mapActions } from "vuex";

export default {
  name: "Question",
  data() {
    return {
      chosen: ""
    };
  },
  methods: {
    ...mapActions(["correctAnswer", "wrongAnswer", "nextQuestion"]),
    saveAnswer(question) {
      const correctAnswer = () => {
        return question.choices.find(choice => choice.correct == true).text;
      };
      if (this.chosen == correctAnswer()) {
        this.correctAnswer(question);
      } else {
        this.wrongAnswer(question);
      }
    },
    saveAndNext(question) {
      this.saveAnswer(question);
      this.nextQuestion();
    },
    saveAndFinish(question) {
      this.saveAnswer(question);
      this.$router.push("finish");
    }
  },
  computed: mapGetters(["selectedQuestion", "nextActive"])
};
</script>

<style lang="sass">
.question
  margin-top: 2rem
  ul li
    margin-bottom: 1rem
  button
    margin-top: 1rem
</style>
