<template>
    <div class="result page">
        <h1>You have finished the exam</h1>
        <ul>
            <li>
                Number of questoins: {{ getSelectedSet.length !== 0 ? getSelectedSet.length : allQuestions.length }}
            </li>
            <li>
                Correct Questions: {{ correctQuestions.length }}
            </li>
            <li>
                Wrong Questions: {{ wrongQuestions.length }}
            </li>
            <li v-if="getSelectedSet.length !== 0">
               Status: <span v-if="correctQuestions.length >= (getSelectedSet.length / 2)">Passed</span> <span v-else>Failed</span>
            </li>
            <li v-else>
               Status: <span v-if="correctQuestions.length >= (allQuestions.length / 2)">Passed</span> <span v-else>Failed</span>
            </li>
            <li v-if="getSelectedSet.length !== 0">
               Score: {{ Math.round(((correctQuestions.length * 100) / getSelectedSet.length)) }}%
            </li>
            <li v-else>
               Score: {{ Math.round(((correctQuestions.length * 100) / allQuestions.length)) }}%
            </li>
        </ul>
        <router-link to="/">
            <button class="route-btn" @click="reset()">Back to home</button>
        </router-link>
    </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
  name: "Finish",
  methods: mapActions(["reset"]),
  computed: mapGetters([
    "getSelectedSet",
    "allQuestions",
    "correctQuestions",
    "wrongQuestions"
  ])
};
</script>

<style lang="sass">
.result
    ul li
        margin-bottom: 1rem
    button
        margin-top: 1rem
</style>

