<template>
    <div class="discipline page">
        <h1>Choose Discipline:</h1>
        <ul>
            <li v-for="discipline in disciplines" :key="discipline.name">
                <router-link to="/lessons">
                    <button class="route-btn" @click="chooseDiscipline(discipline)">
                        {{ discipline }}
                    </button>
                </router-link>
            </li>
        </ul>
    </div>
</template>

<script>
import { mapActions } from "vuex";

export default {
  name: "Practice",
  data() {
    return {
      disciplines: ["anatomy"]
    };
  },
  methods: {
    ...mapActions(["setDiscipline"]),
    chooseDiscipline(discipline) {
      this.setDiscipline(discipline);
    }
  }
};
</script>

<style lang="sass" scoped>
.discipline button
  text-transform: capitalize
</style>
