<template>
    <div class="lessons page">
      <h1>Choose a lesson in <span>{{ getDiscipline }}</span></h1>
      <ul>
            <li v-for="lesson in filteredLessons" :key="lesson.name">
                <router-link to="/sets">
                    <button class="route-btn" @click="setLesson(lesson.name)">
                        {{ lesson.name }}
                    </button>
                </router-link>
            </li>
        </ul>
    </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
  name: "Lessons",
  data() {
    return {
      lessons: [
        {
          discipline: "anatomy",
          name: "Upper limb"
        }
      ]
    };
  },
  methods: mapActions(["setLesson"]),
  computed: {
    ...mapGetters(["getDiscipline"]),
    filteredLessons() {
      const result = this.lessons.filter(
        e => e.discipline.toLowerCase() === this.getDiscipline
      );
      return result;
    }
  }
};
</script>

<style lang="sass">
.lessons h1 span
  text-transform: capitalize
</style>
