<template>
    <div class="sets page">
        <h1>Choose a set of questions</h1>
        <ul>
            <li>
                <router-link to="/exam">
                    <button class="route-btn">
                        All Questions ({{ allQuestions.length }})
                    </button>
                </router-link>
            </li>
            <li v-for="(set, index) in chunkArray" :key="set[0].text">
                <router-link to="/exam">
                    <button class="route-btn" @click="chooseSet(chunkArray[index])">
                        Questions ({{(index+1) + (index * 10) - index }} - {{(index+1) * 10}})
                    </button>
                </router-link>
            </li>
        </ul>
    </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
  name: "Sets",
  methods: {
    ...mapActions(["fetchQuestions", "chooseSet"])
  },

  computed: {
    ...mapGetters(["allQuestions"]),
    chunkArray() {
      const myArray = this.allQuestions;
      const chunk_size = 10;

      var index = 0;
      var arrayLength = myArray.length;
      var tempArray = [];

      for (index = 0; index < arrayLength; index += chunk_size) {
        let myChunk = myArray.slice(index, index + chunk_size);
        tempArray.push(myChunk);
      }
      return tempArray;
    }
  },
  created() {
    this.fetchQuestions();
  }
};
</script>

<style lang="sass">
.sets
    ul li
        margin-bottom: 1rem
        .route-btn
            width: 100%
</style>
